.about-content {
  margin-left: 20%;
  margin-right: 20%;
  text-align: left;
  width: 60%;
}

.about-header {
  font-size: 2em;
  text-align: left;
}
.header {
  text-align: left;
  width: 60%;
  margin-left: 20%;
  margin-right: 20%;
}

.header-title {
  font-size: 2em;
  /* font-weight: lighter; */
  font-weight: normal;
  letter-spacing: 3px;
  margin-bottom: 5vh;
  margin-top: 14vh;
}

.page-link {
  border-bottom: 1px solid #aaa;
  color: #fafafa;
  display:inline-block;
  padding-bottom: 2px;
}

.page-link:hover {
  cursor: pointer;
}

.home-svg {
  fill: none;
  stroke: rgb(158, 198, 184) !important;
  stroke-width: 1px;
  margin: 0;
  position: fixed;
  left: 20%;
  top: 20%;
  z-index: -1;
}

.home-links {
  text-align: left;
  margin-left: 20%;
}

.home-link {
  margin-left: 10px;
  margin-top: 10px;
}

body {
  background-color: #282c34;
  color: #fafafa;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  color: inherit;
  text-decoration: inherit;
}

.app {
  text-align: center;
}


